body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* position attributes */
.inputContainerDiv{
    background-color: #1e2324;
    color: #f0ecd7;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 100vh;
}

.inputCapDiv{
    flex: 1 1;
}

.inputInnerDiv{
    flex: 5 1;
    width: 50%;
}

/* other aspects */ 
.inputInstructionParagraph{
    text-align: center;
    font-size: 1.5em;
}

.errorMessage{
    color: #bf3b33;
}

.overallFormDiv{
    width: 100%; 
    max-width: 900px;
}

.formTitleDiv{
    margin: auto; 
    text-align: center;
}

.textForm{
    margin: auto;
    width: 100%;
}

.textField{
    box-sizing: border-box;
    width: 100%; 
    min-height: 200px;
    min-width: 400px;
    font-size: calc( 5px + 2vmin);
    font-family: Arial, Helvetica, sans-serif;
}

.textTitle{
    margin: 10px;
    font-size: calc(10px + 2vmin);
    width: 70%;
    min-width: 400px;
}
#clozeIntro{
    margin: 2% auto 0px;
}

.targetTitle{
    margin: 15px auto 0px;
}

.ClozeDiv{
    max-height: 100vh;
}

.wordSpan:hover{
    color: brown; 
    cursor: pointer;
}

.displayForVocab{
    padding: 5px 10px 5px 10px;
    background-color: #f0ecd7;
    color: black;
    border-radius: 5px;
}

.displayTargetText{
    max-width: 80%;
}

.newVocab{
    min-width: 400px;
    font-size: calc(10px + 2vmin);
}
#buttonDiv{
    background-color: #1e2324;
    width: 100%; 
    display: flex;
    justify-content: flex-end;
}


.clozeButton{
    text-align: center;
    padding: 10px; 
    margin: 10px; 
}

.finalForm{
    background-color: white;
}

.finalForm-header {
  background-color: white;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: #f5f8f5;
}

#finalTitle{
    text-align: center;
    color: black;
}

.textToPrint{
    font-size: 12px;
    font-family: 'Times New Roman', Times, serif; 
}

#printComponentDiv{
    max-width: 75%;
    color: black;
    padding: 20px; 
}
.App {
  text-align: center;
}

.App-header {
  background-color: #1e2324;
  min-height: 100vh; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #f0ecd7;
}

.landingDiv{
  width: 60%;
}

#landingListDiv{
  width: 70%;
  text-align: left;
  margin: auto;
}

li{
  padding: 5px;
}

.submitButton{
    margin: 7px;
    padding: 5px;
    background-color: #6e756d;
    font-size: calc(10px + 2vmin);
    border-radius: 5px;
}

.submitButton:hover{
    background-color: #bf3b33;
    color: #f5f8f5;
    cursor: pointer; 
}


button{
  border: 1px solid transparent;
}

.generalInput{
    padding: 5px;
    background-color: #f0ecd7;
    border-radius: 5px;
 }
